






























































































































































































































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Home extends Vue {
  private publicPath =
    process.env.NODE_ENV === 'production' ? '/dropper.mood/' : '/';

  private forcedHide(dropper: string = 'popup') {
    const popup = this.$refs[dropper] as any;
    if (popup) {
      popup.close();
    }
  }
}
